}

.eduAndExpDiv {
    width: 90%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 3%;
}

.yearsFormDiv {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 50px;
    margin-bottom: 20px;
}



